import React,{Component} from 'react';
import { Navbar, NavbarBrand, Nav, NavItem, Collapse, NavbarToggler,UncontrolledDropdown,DropdownToggle,DropdownMenu,DropdownItem } from 'reactstrap';
import {NavLink} from 'react-router-dom'
import '../../CustomStyle/style.css';
import { Justify } from 'react-bootstrap-icons';
import headerlogo from '../../image/headerlogo.jpg';


class Navigation extends Component{


    constructor(props) {
        super(props);
        this.state = {
            isOpen:false
        }
    }


    toggle = () => {
        
        this.setState({
            isOpen:!this.state.isOpen
        })

    }

  
  

    render() {
      
    return (
        <Navbar  style={{color:'#FFFFFF'}} expand="md">
            <div className="container">
                <NavbarBrand href="/"><img src={headerlogo} alt="logo.jpg"/></NavbarBrand>
                 <NavbarToggler color="dark" onClick={this.toggle}><Justify color="dark"/></NavbarToggler>
             <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="m-auto" navbar>
            <NavItem>
              <NavLink to="/homepage" className="text-dark nav-link useforFont homePageDefault">HOME</NavLink>
           </NavItem>        
            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle className="text-dark useforFont" nav caret>
                CLUB-LIFE
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem className="apex">
                 <NavLink to="/clublif/epage" className="text-dark " style={{textDecoration:'none'}}> Club-Life-1</NavLink>
                </DropdownItem>
                <DropdownItem>
                Club-Life-2
                </DropdownItem>
                <DropdownItem divider />
                <DropdownItem>
                Club-Life-3
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle className="text-dark useforFont" nav caret>
                TRAINING
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem>
                <NavLink to="/traning/page" className="text-dark " style={{textDecoration:'none'}}> Traning-1</NavLink>
                </DropdownItem>
                <DropdownItem>
                Traning-2
                </DropdownItem>
                <DropdownItem divider />
                <DropdownItem>
                Traning-3
                </DropdownItem>
              </DropdownMenu>
         </UncontrolledDropdown>
          <UncontrolledDropdown nav inNavbar>
                <DropdownToggle className="text-dark useforFont" nav caret>
                  INSTRUCTOR
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem>
                  <NavLink to="/instructor/page" className="text-dark " style={{textDecoration:'none'}}> Traning-1</NavLink>
                  </DropdownItem>
                  <DropdownItem>
                    Option 2
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>
                    Reset
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
              <NavItem>
              <NavLink to="/contact/page" className="text-dark nav-link useforFont">CONTACT</NavLink>
            </NavItem>            
          </Nav>
        </Collapse> 
           </div>
      </Navbar>
    );
  }

}



export default Navigation