import React from 'react';
import clubbigicon from '../../image/clubbigicon.png';
import clubmembericon from '../../image/clubmembericon.png';
import readicon from '../../image/readicon.png';



const Clubcomponent = () => {

    return (
        <div className="container">
            <div className="row justify-content-end">
                <div className="col-6 mt-5">
                  <p>The Harrison twins are International Fitness Models and Personal Trainers. They have been training now for a number of years and have a vast amount of knowledge on diet and training and how to get the best results possible for their clients.</p>
              </div>
            </div>
            <div className="row">
                <div className="col-5 m-auto mt-3">
                    <img src={clubbigicon} alt="clubbigicon.png"
                     width="330px" height="330px"
                    />
             </div>
            </div>
            <div className="row">
                <div className="col-7 m-auto mt-5">
                <img src={clubmembericon} alt="clubbigicon.png" className="w-100"
                       /> 
              </div>
            </div>
            <div className="row mt-5">
                <div className="col-md-6 col-sm-12 m-auto mt-5" style={{borderBottom:'2px solid #EEEDED'}}>
                    <div className="d-flex">
                        <img src={readicon} alt="readicon.png" width="30px" height="30px" className="m-2"/>
                        <p className="font-weight-normal">Why a high protein diet of 1.5g per pound of bodyweight has significant muscle building and weight loss benefits without any harmful effects.</p>
                    </div>
                </div>
            </div>
            <div className="row mt-2">
                <div className="col-md-6 col-sm-12 m-auto " style={{borderBottom:'2px solid #EEEDED'}}>
                    <div className="d-flex">
                        <img src={readicon} alt="readicon.png" width="30px" height="30px" className="m-2"/>
                        <p className="font-weight-normal">Why a high protein diet of 1.5g per pound of bodyweight has significant muscle building and weight loss benefits without any harmful effects.</p>
                    </div>
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-md-6 col-sm-12 m-auto " style={{borderBottom:'2px solid #EEEDED'}}>
                    <div className="d-flex">
                        <img src={readicon} alt="readicon.png" width="30px" height="30px" className="m-2"/>
                        <p className="font-weight-normal">Why my supplement company, JYM Supplement Science forced the industry to change. Ending common practices like protein spiking, prop formulas, and concentrated formulas.</p>
                    </div>
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-md-6 col-sm-12 m-auto " style={{borderBottom:'2px solid #EEEDED'}}>
                    <div className="d-flex">
                        <img src={readicon} alt="readicon.png" width="30px" height="30px" className="m-2"/>
                        <p className="font-weight-normal">And much, much more that you can discover when you take your free 7 day trial membership as well as the app included with your membership.</p>
                    </div>
                </div>
            </div>
        </div>
    );
    
}


export default Clubcomponent;