import React from 'react';
import {Switch,Route,Redirect} from 'react-router-dom';
import Home from '../pages/Home';
import Club from '../pages/Club';
import Traning from '../pages/Traning';
import Instructor from '../pages/Instructor';
import Contact from '../pages/Contact';


const Body = () => {
    
    return (
        <div>
            <Switch>
                <Route path="/homepage" exact render={() => <Home />} />
                <Route path="/clublif/epage" exact render={() => <Club />} />
                <Route path="/traning/page" exact render={() => <Traning />} />
                <Route path="/instructor/page" exact render={() => <Instructor />} />
                <Route path="/contact/page" exact render={()=><Contact/>} />
                <Redirect from="/" to="/homepage" />
            </Switch>
            
        </div>
    );

}



export default Body