import React from 'react';
import userprofile from '../../image/userprofile.png';


const Processcomponent = () => {
    
    return (
        <div className="container-fluied">
        <div className="container">
            <div className="row mt-5">
                <div className="col-md-6 col-sm-12 m-auto">
                    <h3 className="text-center text-dark text-uppercase display-5">3 Step Process to</h3>
                    <h3 className="text-center text-danger text-uppercase display-5">get started</h3>
                </div>
            </div>
            <div className="row">
                <div className="col-md-5 col-sm-12 m-auto">
                    <p className="text-center text-dark mt-2">Follow these 3 simple steps to start your journey to a fitter, happier and healthier lifestyle.</p>
               </div>
            </div>
            <div className="row">
                <div className="col-md-8 col-sm-12 m-auto mt-5">
                    <div className="row">
                        <div className="col-md-4 col-sm-12">
                            <img src={userprofile} alt="userprofile.png" width="150px" height="150px"
                            className="mx-auto d-block"/>
                            <h3 className="text-center text-uppercase mb-2 mt-2">Step <strong className="text-danger">one</strong></h3>
                            <p className="text-center">My Super-Man program uses super sets in which exercises for opposing muscle groups are paired together. With SS8, the supersets pair exercises for the same muscle group.</p>
                        </div>
                        <div className="col-md-4 col-sm-12">
                            <img src={userprofile} alt="userprofile.png" width="150px" height="150px"
                             className="mx-auto d-block"/>
                            <h3 className="text-center text-uppercase mb-2 mt-2">Step <strong className="text-danger">two</strong></h3>
                            <p className="text-center">My Super-Man program uses super sets in which exercises for opposing muscle groups are paired together. With SS8, the supersets pair exercises for the same muscle group.</p>
                        </div>
                        <div className="col-md-4 col-sm-12">
                            <img src={userprofile} alt="userprofile.png" width="150px" height="150px"
                             className="mx-auto d-block"/>
                            <h3 className="text-center text-uppercase mb-2 mt-2">Step <strong className="text-danger">three</strong></h3>
                            <p className="text-center">My Super-Man program uses super sets in which exercises for opposing muscle groups are paired together. With SS8, the supersets pair exercises for the same muscle group.</p>
                        </div>
                   </div>
               </div>
            </div>
            </div>
            <div className="container-fluied" style={{backgroundColor:"#E74C3C"}}>
                <div className="container">
                    <div className="row mt-5">
                        <div className="col-md-5 col-sm-12 m-auto mt-5">
                           <h3 className="text-center text-light text-uppercase display-5">Get in shape now!</h3>
                        </div>
                    </div>
                      <div className="row">
                        <div className="col-md-5 col-sm-12 m-auto mt-3">
                           <p className="text-light text-center">Join now for a free 7 day trial</p>
                        </div>
                    </div>
                     <div className="row mt-5">
                        <div className="col-md-5 col-sm-12 m-auto mb-5">
                            <h3 className="text-light text-uppercase text-center">GET STARTED!</h3>
                       </div>
                    </div>
                </div>
            </div>
      </div>
    );

}



export default Processcomponent;