import React from 'react';
import '../../CustomStyle/style.css';
import sliderbanner from '../../image/sliderbanner.jpg'
import bodyicon1 from '../../image/bodyicon1.jpg'
import middlebox from '../../image/middlebox.jpg'
import bodyicon2 from '../../image/bodyicon2.jpg'

const Slide = () => {
    

    return (
    
             <div className="sliderWrap">
            <div className="sliderMain">
                <img src={sliderbanner} alt="sliderbanner.jpg" width="100%" height="100%"/>
            </div>
            <div className="w-75 sliderHighLight">
                <div className="row d-flex justify-content-between  align-items-center flex-wrap">
                    <div className="col-sm-4 col-md-4">
                        <div className="d-flex justify-content-center">
                            <div className="p-2">
                             <img src={bodyicon1} alt="bodyicon1.jpg" className="rounded"/>
                            </div>
                            <div>
                                <p>MuscleMania<br /> Universe Natural</p>
                                <span><small className="text-danger"  style={{cursor:'pointer'}}>VIEW MORE</small></span>
                            </div>
                       </div>
                    </div>

                     <div className="col-sm-4 middleBox">
                     <img src={middlebox} alt="middlebox.jpg" className="img-thumbnail border-0"/>
                    </div>

                     <div className="col-sm-4 col-md-4">
                     <div className=" w-100 d-flex justify-content-start lastBody">
                            <div className="p-2">
                             <img src={bodyicon2} alt="bodyicon2.jpg" className="rounded"/>
                            </div>
                            <div className="p-2">
                                <p>Accredited Master<br />Trainer</p>
                                <span><small className="text-danger" style={{cursor:'pointer'}}>VIEW MORE</small></span>
                            </div>
                       </div>
                    </div>
                </div>
            </div>
            </div>
        
    );

}



export default Slide